
import Group from 'vux/src/components/group/index.vue'
import Cell from 'vux/src/components/cell/index.vue'
import XHeader from 'vux/src/components/x-header/index.vue'
import InlineXNumber from 'vux/src/components/inline-x-number/index.vue'
import XProgress from 'vux/src/components/x-progress/index.vue'
import Box from 'vux/src/components/box/index.vue'

import FlexBox from './FlexBox'

export default {
components: {
Group,
Cell,
XHeader,
InlineXNumber,
FlexBox,
XProgress,
Box
},
data () {
return {
map:{
food:0,
tree:0,
grass:0,
iron:0
},
info:{
food:{
lastTime:0,
title:"1名食物仆役每次增加1点食物",
add:1,
number:0,//加减数字
remainingTime:0,
name:{
key:"食物",
value:1
},
in:{
key:"储量",
now:0,
max:450
},
out:{
key:"产量",
value:0
}
},
tree:{
lastTime:0,
title:"1名木材仆役每次消耗2点食物",
add:2,
number:0,//加减数字
remainingTime:0,
name:{
key:"木材",
value:1
},
in:{
key:"储量",
now:0,
max:180
},
out:{
key:"产量",
value:0
}
},
grass:{
lastTime:0,
title:"1名灵草仆役每次消耗4点食物",
add:4,
remainingTime:0,
number:0,//加减数字
name:{
key:"灵草",
value:1
},
in:{
key:"储量",
now:0,
max:90
},
out:{
key:"产量",
value:0
}
},
iron:{
lastTime:0,
title:"1名玄铁仆役每次消耗4点食物",
add:4,
remainingTime:0,
number:0,//加减数字
name:{
key:"玄铁",
value:1
},
in:{
key:"储量",
now:0,
max:90
},
out:{
key:"产量",
value:0
}
}
},
percent:100
}
},
mounted(){
console.log("已挂在！");
setInterval(()=>{
if(this.percent==0){
this.percent = 100;
this.addSource();
}else{
this.percent --;
}
},100);
this.getLocal();
},
methods:{
updateInfo(){//根据等级计算资源
for(let key in this.info){
const item = this.info[key];
item.in.max = 90 * item.name.value * this.getMax(item,key);
}
},
uplevel(key){//资源升级
this.info[key].name.value ++;
this.updateInfo();
},
getMax(item,key){

if(key=="food"){
return 3*item.name.value + 2
}
if(key=="tree"){
return  2*item.name.value
}
return item.name.value;
},
getLocal(){
let info = localStorage.getItem("xz_Info");
if(info){
info = JSON.parse(info);
info = this.resetInfo(info);
this.info = info;
}
},
resetInfo(info){
for(let key in info){
const item = info[key];
let now = new Date().getTime();
let c = (now - item.lastTime)/1000;
let num = Math.floor(c/10);
let t = Number(item.in.now) + Number(item.out.value * num);
if(t>item.in.max) t = item.in.max;
if(t<0) t=0;
item.in.now = t;
}
return info;
},
changeInput(v,item,type){
if(type==1){
item.now = v;
}
if(type==2){
item.max = v;
}
if(type==3){
item.value = v;
this.updateInfo();
}
},
changeValue(v,item){
item.number = v;
this.calculateOut();
this.calculateTime();
this.setLocal();
},
calculateTime(){
let time = 10;
let isOut = false;
if(this.info.food.in.now<=0 && this.info.food.out.value<0){
isOut = true;
}
for(let key in this.info){
const item = this.info[key];
if(item.out.value==0){
item.remainingTime = 0;
}else{
let last = item.in.max-item.in.now;
let t = item.out.value;
if(item.out.value<0){
last = Math.abs(item.in.now);
t = Math.abs(item.out.value);
}


item.remainingTime = Math.floor((last/t)*10);
}
if(isOut){
item.remainingTime = 0;
}
}
this.setLocal();
},
setLocal(){
for(let key in this.info){
const item = this.info[key];
item.lastTime = new Date().getTime();
}
localStorage.setItem("xz_Info",JSON.stringify(this.info));
},
calculateOut(){
let info = this.info;
let total = 0;
let mtotal = 0;
for(let key in info ){
let item = info[key];

item.out.value = item.number;
if(key!="food"){
total += item.number*item.add;
}else{
mtotal = item.number*item.add;
}
}
info.food.out.value  = mtotal-total;
},
addSource(){
let info = this.info;

for(let key in info){
if(key=="food" && info[key].in.now<=0 && info[key].out.value<0) break;
let item = info[key];
let now = Number(item.in.now) + Number(item.out.value);
if(now <0) now = 0;
if(now>=item.in.max){
now = item.in.max;
}
item.in.now = now;
}
this.calculateTime();
}
},
watch:{
'info.food.name.value':(newValue)=>{
console.log("改变的value",newValue);
}
}
}



