export default {
  name:"FlexBox",
  props:{
    info:Object,
    keyName:String
  },
  components: {

  },
  data () {
    return {
      v1: this.info.in.now,
      v2:this.info.in.max,
      v3:this.info.name.value
    }
  },
  methods:{
    upLevel(){
        this.$emit("updateLevel",this.keyName);
    },
    getTextByTime(s){
        // console.log("剩余秒数：",s);
        let m=0;
        let h=0;
        let ss = 0;
        if(s<60){
            m=0;h=0;ss=s;
        }else if(s<3600){
            h=0;
            m = Math.floor(s/60);
            ss = s%60;
        }else{
            h = Math.floor(s/3600);
            m = Math.floor((s-h*3600)/60);
            ss = Math.floor((s-h*3600-m*60));
        }
        return `${h}小时${m}分钟${ss}秒`;
    }
  },
  watch:{
      "info.in.now"(val){
        this.v1 = val;
      },
      "info.in.max"(val){
        this.v2 = val;
      },
      "info.name.value"(val){
          this.v3 = val;
      },
      v1(value){
        this.$emit("input1",Number(value));
      },
      v2(value){
        this.$emit("input2",Number(value));
      },
      v3(value){
          this.$emit("input3",Number(value))
      }
  }
}



